import React, { Component } from 'react';
import './App.css';

import Favorites from './components/Favorites';
import Cities from './components/Cities';

class App extends Component {
    render() {
        return (
            <div className="App">
                <Favorites />
                <Cities />
            </div>
        );
    }
}

export default App;



