import React, { Component } from 'react';
import { connect } from 'react-redux';
import './Cities.css';

import CityAction from '../actions/CityAction';
import FavoriteAction from '../actions/FavoriteAction';

class Content extends Component {
    constructor(props) {
        super(props);
        this.props.onGeolocation();
    }

    getInput(input) {
        this.cityInput = input;
    }

    onKeyPress(event){
        if (event.key === "Enter") {
            this.onAddButtonClick();
        }
    }

    onAddButtonClick() {
        this.props.onAddCity(this.cityInput.value);
        this.cityInput.value = '';
    }

    onRemoveButtonClick(event) {
        this.props.onRemoveCity(event.target.id);
    }

    onFavoriteButtonClick(event){
        this.props.onFavoriteAdd(event.target.id);
    }
    
    render() {
        return (
            <div className="Content">
                <div className="Header">
                    <div className="Title">Weather</div>
                    <div className="Search">
                        <input type="text" placeholder="Enter city name for search" ref={this.getInput.bind(this)} onKeyPress={this.onKeyPress.bind(this)} />
                        <button onClick={this.onAddButtonClick.bind(this)}>Find the city</button>
                    </div>
                </div>
                <div className="ContentList">
                    <ul>
                        {
                            this.props.store.cities.map((value, index) => {
                                return (
                                    <li key={index}>
                                        <div className="City">
                                            <img className="CityImage" src={value.icon} />
                                            <div className="CityName">{value.name}</div>
                                            <div className="CityCountry">{value.country}</div>
                                            <button className="CityRemove" id={value.id} onClick={this.onRemoveButtonClick.bind(this)}></button>
                                            <button className="CityAddFavorite" id={value.name} onClick={this.onFavoriteButtonClick.bind(this)}></button>
                                            <div className="CityTemperature">{value.temp}</div>
                                            <div className="CityWind">Wind: {value.wind}</div>
                                            <div className="CityPressure">Pressure: {value.pressure}</div>
                                            <div className="CityHumidity">Humidity: {value.humidity}</div>
                                        </div>
                                    </li>
                                )
                            })
                        }
                    </ul>
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state, ownProps) => {
    return {
        store: state
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        onGeolocation: () => {
            dispatch(CityAction.geolocation(dispatch));
        },
        onAddCity: (cityName) => {
            dispatch(CityAction.addCity(cityName, dispatch));
        },
        onRemoveCity: (id) => {
            dispatch(CityAction.removeCity(id, dispatch));
        },
        onFavoriteAdd: (id) => {
            dispatch(FavoriteAction.addCity(id, dispatch));
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Content);


