import Weather from '../data/Weather';
import Geolocation from '../data/Geolocation';

class CityAction {
    static geolocation(dispatch) {
        if (!navigator.geolocation) return;

        return dispatch => {
            const weather = new Weather();

            navigator.geolocation.getCurrentPosition((position) => {
                let latitude = position.coords.latitude;
                let longitude = position.coords.longitude;
                const response = weather.getWeatherByLatLon(latitude, longitude);

                response.onload = (data) => {
                    let results = JSON.parse(response.responseText);
                    if (results.name !== '' && results.name !== null && results.name !== undefined) {
                        let tempCity = Math.round(results.main.temp - 273.15);
                        tempCity = (tempCity > 0) ? "+"+tempCity : tempCity;
                    
                        let obj = {
                            id: results.id,
                            name: results.name,                             // 
                            country: results.sys.country,                   // 
                            temp: tempCity,                                 // 
                            pressure: results.main.pressure,                // 
                            humidity: results.main.humidity,                // 
                            wind: results.wind.speed + " m/s",              // 
                            icon: "http://openweathermap.org/img/w/" + results.weather[0].icon + ".png"
                        }

                        dispatch({ type: 'ADD_CITY', payload: obj });
                    }else{
                        alert('This city was not found!');
                    }
                }

            }, (err) => {
                alert("ERROR(" + err.code + ") : " + err.message);
            });
        }
    }

    static addCity(cityName, dispatch) {
        return dispatch => {
            const weather = new Weather();
            const response = weather.getWeatherByName(cityName);
            response.onload = (data) => {
                let results = JSON.parse(response.responseText);
                if (results.name !== '' && results.name !== null && results.name !== undefined) {
                    let tempCity = Math.round(results.main.temp - 273.15);
                    tempCity = (tempCity > 0) ? "+"+tempCity : tempCity;
                    let obj = {
                        id: results.id,
                        name: results.name,                             // 
                        country: results.sys.country,                   // 
                        temp: tempCity,                                 // 
                        pressure: results.main.pressure,                // 
                        humidity: results.main.humidity,                // 
                        wind: results.wind.speed + " m/s",              // 
                        icon: "http://openweathermap.org/img/w/" + results.weather[0].icon + ".png"
                    }
                    dispatch({ type: 'ADD_CITY', payload: obj });
                }else{
                    alert('This city was not found!');
                }
            }
        }
    }

    static removeCity(id, dispatch) {
        return dispatch => {
            dispatch({ type: 'REMOVE_CITY', payload: id });
        }
    }
}

export default CityAction;


