import Weather from '../data/Weather';

class FavoriteAction {
    static addCity(cityName, dispatch) {
        return dispatch => {
            const weather = new Weather();
            const response = weather.getWeatherByName(cityName);
            response.onload = (data) => {
                let results = JSON.parse(response.responseText);
                if (results.name !== '' && results.name !== null && results.name !== undefined) {
                    let tempCity = Math.round(results.main.temp - 273.15);
                    tempCity = (tempCity > 0) ? "+"+tempCity : tempCity;
                    let obj = {
                        id: results.id,
                        name: results.name,                             // 
                        country: results.sys.country,                   // 
                        temp: tempCity,                                 // 
                        pressure: results.main.pressure,                // 
                        humidity: results.main.humidity,                // 
                        wind: results.wind.speed + " m/s",              // 
                        icon: "http://openweathermap.org/img/w/" + results.weather[0].icon + ".png"
                    }
                    dispatch({ type: 'ADD_SITY_FAVORIT', payload: obj });
                }else{
                    alert('This city was not found!');
                }
            }
        }
    }

    static removeCity(id, dispatch) {
        return dispatch => {
            dispatch({ type: 'REMOVE_CITY_FAVORIT', payload: id });
        }
    }
}

export default FavoriteAction;


