export default function citiesList(state = [], action){
    if(action.type === 'ADD_CITY'){
        let checkDuplicates = state.filter(value => value.name.includes(action.payload.name));
        if(checkDuplicates.length > 0) {
            alert("This is duplicate");
            return state;
        }
        return [
            ...state,
            action.payload
        ]
    }else if(action.type === 'REMOVE_CITY'){
        let newState = state.filter(obj => {
            if(obj.id.toString() !== action.payload){
                return obj
            }
        });
        return newState;
    }
    return state;
}


