import React, { Component } from 'react';
import { connect } from 'react-redux';
import './Favorites.css';

import FavoriteAction from '../actions/FavoriteAction';

class Favorites extends Component {
    constructor(props) {
        super(props);
    }

    getInput(input) {
        this.filterInput = input;
    }

    onKeyPress(event) {
        if (event.key === "Enter") {
            this.props.onFilterCity(this.filterInput.value);
        }
    }

    onRemoveButtonClick(event) {
        this.props.onRemoveCity(event.target.id);
    }

    onFilterButtonClick(event) {
        this.props.onFilterCity(this.filterInput.value);
    }

    render() {
        return (
            <div className="Favorite">
                <div className="Filter">
                    <input type="text"
                        placeholder="Enter value for filter"
                        ref={this.getInput.bind(this)}
                        onKeyPress={this.onKeyPress.bind(this)}
                    />
                    <button onClick={this.onFilterButtonClick.bind(this)}></button>
                </div>
                <div className="FavoriteList">
                    <ul>
                        {
                            this.props.store.map((value, index) => {
                                return (
                                    <li key={index}>
                                        <div className="FavoriteCity">
                                            <img className="FavoriteCityImage" src={value.icon} />
                                            <div className="FavoriteCityTemperature">{value.temp}</div>
                                            <div className="FavoriteCityName">{value.name}</div>
                                            <button className="FavoriteCityButton" id={value.id} onClick={this.onRemoveButtonClick.bind(this)}></button>
                                        </div>
                                    </li>
                                )
                            })
                        }
                    </ul>
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state, ownProps) => {
    return {
        store: state.favorites.filter(value => value.name.includes(state.filter))
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        onRemoveCity: (id) => {
            dispatch(FavoriteAction.removeCity(id, dispatch));
        },
        onFilterCity: (value) => {
            dispatch({ type: 'FILTER_FAVORIT', payload: value });
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Favorites);



