export default class Weather {
    constructor() {
        this.OPENWEATHER_URL = 'http://api.openweathermap.org/data/2.5/weather';
        this.APPID = 'da06cf8106afb34ae1142a4beb9ed1aa';
    }

    getWeatherByLatLon(lat, lon) {
        let requestString = this.OPENWEATHER_URL + '?lat=' + lat + '&lon=' + lon + '&APPID=' + this.APPID;
        let request = new XMLHttpRequest();
        request.open("get", requestString, true);
        request.send();
        return request;
    }

    getWeatherByName(cityName) {
        let requestString = this.OPENWEATHER_URL + '?q=' + cityName + '&APPID=' + this.APPID;
        let request = new XMLHttpRequest();
        request.open("get", requestString, true);
        request.send();
        return request;
    }

}


